            show-expand
          >
           <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                {{ item.notes }}
              </td>
            </template>
            <template v-slot:item.TypeID="{ item }">
              <td class="text-right">
                <span
                  v-if="item.TypeID == 1"
                  class="label label-lg label-light-default label-inline"
                >
                  الوصف
                </span>
                <span
                  v-if="item.TypeID == 2"
                  class="label label-lg label-light-warning label-inline"
                >
                  المميزات
                </span>
                <span
                  v-if="item.TypeID == 3"
                  class="label label-lg label-light-success label-inline"
                >
                  الموقع
                </span>
                <span
                  v-if="item.TypeID == 4"
                  class="label label-lg label-light-info label-inline"
                >
                  المساحات
                </span>
              </td>
            </template>
            <template v-slot:item.actions="{ item }">
              <v-icon @click="viewImage(item)">fa fa-image</v-icon>
              
              <a
                @click="editItem(item)"
                v-scroll-to="'#formData'"
                class="btn btn-icon btn-light btn-sm mx-3"
              >
                <span class="svg-icon svg-icon-md svg-icon-primary">
                  <svg
                    version="1.1"
                    viewBox="0 0 24 24"
                    height="24px"
                    width="24px"
                    xmlns:xlink="http://www.w3.org/1999/xlink"
