              <v-icon @click="viewMap(item)">fa fa-map</v-icon>
            </template>
          </v-data-table>
        </v-card>
      </div>
    </div>

    <hr id="formData" />
    <div class="row">
      <div class="col-md-12">
        <v-card>
          <v-card-title>
            Fill in Form Data
          </v-card-title>
          <v-card-text>
            <validation-observer ref="observer" v-slot="{ handleSubmit }">
              <b-form @submit.stop.prevent="handleSubmit(onSubmit)">
                <validation-provider
                  name="plan"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label="Plans">
                    <b-form-input
                      v-model="form.name"
                      :state="getValidationState(validationContext)"
                      aria-describedby="plan"
                    ></b-form-input>
                    <b-form-invalid-feedback id="plan">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <validation-provider
                  name="image"
                  rules=""
                  v-slot="validationContext"
                >
                  <b-form-group label="Plan's Image">
                    <!-- Styled -->
                    <b-form-file
                      accept="image/*"
                      v-model="form.image"
                      placeholder="Choose a file or drop it here..."
                      drop-placeholder="Drop file here..."
                      :state="getValidationState(validationContext)"
                      aria-describedby="image"
                      ref="image"
                      v-on:change="onFileChange"
                    ></b-form-file>
                    <b-form-invalid-feedback id="image">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <validation-provider
                  name="GoogleUrl"
                  rules=""
                  v-slot="validationContext"
                >
                  <b-form-group label="GoogleMap URL">
                    <b-form-input
                      v-model="form.GoogleUrl"
                      :state="getValidationState(validationContext)"
                      aria-describedby="GoogleUrl"
                    ></b-form-input>

                    <b-form-invalid-feedback id="GoogleUrl">{{
                      validationContext.errors[0]
                    }}</b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>

                <validation-provider
                  name="distance"
                  rules="integer"
                  v-slot="validationContext"
                >
                  <b-form-group label="Distance">
                    <b-form-input
                      v-model="form.distance"
                      :state="getValidationState(validationContext)"
                      aria-describedby="Distance"
                    ></b-form-input>

                    <b-form-invalid-feedback id="Distance">{{
                      validationContext.errors[0]
                    }}</b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <validation-provider
                  name="Description"
                  rules=""
                  v-slot="validationContext"
                >
                  <b-form-group label=" Description" label-for="desc">
                    <b-form-textarea
                      id="description"
                      name="description"
                      v-model="form.description"
                      placeholder="Enter something..."
                      rows="3"
                      max-rows="6"
                      :state="getValidationState(validationContext)"
                      aria-describedby="input-3-live-feedback"
                    ></b-form-textarea>

                    <b-form-invalid-feedback id="input-3-live-feedback">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <b-button type="submit" variant="success">Save</b-button>
                <b-button class="ml-2" variant="warning" @click="resetForm()"
                  >Reset</b-button
                >
              </b-form>
            </validation-observer>
          </v-card-text>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

const VueScrollTo = VueScrollTo;

export default {
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [{ title: "Plans", route: "plans" }]);
  },
  data() {
    return {
      records: [],
      plans: [],
      edit: false,
      record_id: "",
      form: {
        name: null,
        image: null,
        GoogleUrl: "",
        distance: 0,
        description: null,
      },
      filter: {
        plan: null,
      },
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "Name", value: "name" },
        //{ text: "Notes", value: "notes" },
        { text: "Distance", value: "distance" },
        { text: "Actions", value: "actions", sortable: false },
        // { text: "GoogleUrl", value: "GoogleUrl" },
      ],
    };
  },

  created() {
    this.fetchText();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        console.log("no files");
      }

      const file = files[0];
      // this.url = URL.createObjectURL(file);
      this.form.image = file;
    },
    viewImage(item) {
      if (item.img === null) {
        this.$confirm({
          title: "Message",
          message: `There is no Image to be viewed`,
          button: {
            no: "Ok",
          },
        });
      } else window.open(item.img);
    },
     viewMap(item) {
      if (item.GoogleUrl === null || item.GoogleUrl === "null") {
        this.$confirm({
          title: "Message",
          message: `There is no Google Map URL to be viewed`,
          button: {
            no: "Ok",
          },
        });
      } else window.open(item.GoogleUrl);
    },
    resetForm() {
      this.form = {
        name: null,
        image: null,
        GoogleUrl: null,
        distance: null,
        description: null,
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchText() {
      this.$ApiService
        .get("/Plans")
        .then((response) => {
          this.records = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    applyFilter() {
      this.$ApiService
        .post("/Plans/Filter", this.filter)
        .then((response) => {
          this.records = response.data.data;
        })
        .catch((err) => {
          Vue.$toast.error("get records failed");
          console.log(err);
        });
    },

    editPlanData() {
      let formData = new FormData();
      for (var key in this.form) {
        formData.append(key, this.form[key]);
      }
      this.$ApiService
        .post(`/Plan/Update/${this.record_id}`, formData)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();

            this.$refs.table.scrollIntoView();
            this.fetchText();
            Vue.$toast.success(`${response.data.result}`);
          } else {
            console.log(response.data);
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    insert() {
       let formData = new FormData();
      for (var key in this.form) {
        formData.append(key, this.form[key]);
      }
      this.$ApiService
        .post("/Plan/New", formData)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();

            this.$refs.table.scrollIntoView();
            this.fetchText();
            Vue.$toast.success(`${response.data.result}`);
          } else {
            console.log(response.data);
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    onSubmit() {
      if (this.edit === false) {
        // add new record
        this.insert();
      } else {
        //update record
        this.editPlanData();
      }
    },
    addItem() {
      this.edit = false;
      this.resetForm();
    },
    editItem(item) {
      this.edit = true;
      this.record_id = item.id;
      this.form.name = item.name;
      this.form.description = item.notes;
      this.form.GoogleUrl = item.GoogleUrl;
      this.form.distance = item.distance;
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Plans/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchText();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => Vue.$toast.error(`${err}`));
          }
        },
      });
    },
  }, // end of methods
}; //end of class
</script>
